<script>
import apiUtils from "./ApiUtil.vue";

const historyUtils = {
  // 
  //comicId id  chapter:id  page:
  //  { comicId: '456', chapter: '3', page: '2',comicTitle:'xxx',updateChapter:'xxx',icon:'xxx',author:'xxx' },
  readingHistory: { value: [] },
  // 
  getLocalReadingHistory: async function () {
    const localHistory = localStorage.getItem("readingHistory");
    if (localHistory) {
      this.readingHistory.value = JSON.parse(localHistory);
    }
  },

  // 
  saveLocalReadingHistory: function () {
    localStorage.setItem(
      "readingHistory",
      JSON.stringify(this.readingHistory.value)
    );
  },

  // 
  mergeReadingHistoryOnLogin: async function () {
    // 
    this.getLocalReadingHistory();
    // 
    const cloudHistory = await apiUtils.getHistory();

    //    { comicId: '456', chapter: '3', page: '2',comicTitle:'xxx',updateChapter:'xxx',icon:'xxx',author:'xxx' }
    const allHistory = [...cloudHistory, ...this.readingHistory.value];
    //

    //  comicId 
    const groupedHistory = allHistory.reduce((acc, record) => {
      if (!acc[record.comicId]) {
        acc[record.comicId] = record;
      } else if (
        parseInt(record.chapter) > parseInt(acc[record.comicId].chapter)
      ) {
        acc[record.comicId] = record;
      } else if (
        parseInt(record.chapter) === parseInt(acc[record.comicId].chapter) &&
        parseInt(record.page) > parseInt(acc[record.comicId].page)
      ) {
        acc[record.comicId] = record;
      }
      return acc;
    }, {});

    // 
    this.readingHistory.value = Object.values(groupedHistory);

    // 
    this.saveLocalReadingHistory();

    // 
    if (!apiUtils.checkLogin()) {
      return;
    }

    //
    // 
    const newRecords = this.readingHistory.value.filter(
      (record) => !cloudHistory.some((r) => r.comicId === record.comicId)
    );
    // 
    const updatedRecords = this.readingHistory.value.filter((record) =>
      cloudHistory.some(
        (r) =>
          r.comicId === record.comicId &&
          (parseInt(r.chapter) < parseInt(record.chapter) ||
            (parseInt(r.chapter) === parseInt(record.chapter) &&
              parseInt(r.page) < parseInt(record.page)))
      )
    );
    // 
    const recordsToSync = [...newRecords, ...updatedRecords];
    // 
    if (recordsToSync.length > 0) {
      await apiUtils.pushHistory({ records: recordsToSync });
    }
  },
  // 
  insertOrUpdate: async function (newRecord) {
    //
    newRecord.time = new Date().getTime();
    //idlong
    newRecord.comicId = parseInt(newRecord.comicId);
    // 
    this.getLocalReadingHistory();
    // 
    const existingRecordIndex = this.readingHistory.value.findIndex(
      (record) => record.comicId === newRecord.comicId
    );
    //
    var needUpdateCloud = false;
    // 
    if (existingRecordIndex !== -1) {
      const existingRecord = this.readingHistory.value[existingRecordIndex];
      const isNewChapter =
        parseInt(newRecord.chapter) != parseInt(existingRecord.chapter);
      const isNewPage =
        parseInt(newRecord.chapter) === parseInt(existingRecord.chapter) &&
        parseInt(newRecord.page) != parseInt(existingRecord.page);
      // 
      if (isNewChapter || isNewPage) {
        this.readingHistory.value[existingRecordIndex] = newRecord;
        needUpdateCloud = true;
      }
    } else {
      needUpdateCloud = true;
      //  
      this.readingHistory.value.unshift(newRecord);
    }
    // 
    this.readingHistory.value.sort((a, b) => {
      if (a.time && b.time) {
        return b.time - a.time;
      } else if (a.time) {
        return -1;
      } else if (b.time) {
        return 1;
      } else {
        return 0;
      }
    });

    if (needUpdateCloud) {
      // 
      this.saveLocalReadingHistory();
      // 
      if (apiUtils.checkLogin()) {
        await apiUtils.pushHistory({ records: [newRecord] });
      }
    }
  },

  // 
  getPaginatedReadingHistory: function (pageIndex, pageSize) {
    const localHistory = localStorage.getItem("readingHistory");
    if (localHistory) {
      const allHistory = JSON.parse(localHistory);
      const startIndex = pageIndex * pageSize;
      const endIndex = startIndex + pageSize;
      const paginatedHistory = allHistory.slice(startIndex, endIndex);
      this.readingHistory.value = paginatedHistory;
    }
    return this.readingHistory.value;
  },
  // 
  getReadingHistoryCount: function () {
    const localHistory = localStorage.getItem("readingHistory");
    if (localHistory) {
      return JSON.parse(localHistory).length;
    } else {
      return 0;
    }
  },

  //  ID 
  deleteReadingHistoryById: function (id) {
    //idlong
    id = parseInt(id);
    const localHistory = localStorage.getItem("readingHistory");
    if (localHistory) {
      console.log(": " + id);
      const allHistory = JSON.parse(localHistory);
      const newHistory = allHistory.filter((record) => record.comicId !== id);
      localStorage.setItem("readingHistory", JSON.stringify(newHistory));
      // 
      if (apiUtils.checkLogin()) {
        apiUtils.deleteHistory({ id: id });
      }
    }
  },
  //
  //{ comicId: '456', chapter: '3', page: '2',comicTitle:'xxx',updateChapter:'xxx',icon:'xxx',author:'xxx' }
  updateReadingHistoryDetails: function (updatedDetails) {
    //idlong
    updatedDetails.comicId = parseInt(updatedDetails.comicId);
    //log
    const localHistory = localStorage.getItem("readingHistory");
    // 
    const localJSON = JSON.parse(localHistory);
    if (!localJSON) {
      return;
    }
    const existingRecordIndex = localJSON.findIndex(
      (record) => record.comicId === updatedDetails.comicId
    );

    // 
    if (existingRecordIndex !== -1) {
      localJSON[existingRecordIndex] = {
        ...localJSON[existingRecordIndex],
        comicTitle: updatedDetails.comicTitle,
        updateChapter: updatedDetails.updateChapter,
        icon: updatedDetails.icon,
        author: updatedDetails.author,
      };
      // 
      localStorage.setItem("readingHistory", JSON.stringify(localJSON));
    }
  },
  //id
  findReadingHistoryById: function (id) {
    //idlong
    id = parseInt(id);
    const localHistory = localStorage.getItem("readingHistory");
    if (localHistory) {
      const allHistory = JSON.parse(localHistory);
      const history = allHistory.find((record) => record.comicId === id);
      return history;
    }
    return null;
  },
};

export default historyUtils;
</script>
