<!--  -->
<template>
  <!--  -->
  <LoadingCmp ref="loading" />
  <!--  -->
  <TopCmp :topTitle="title" />

  <article class="main under-top-bar">
    <div class="mod-his">
      <!--  -->
      <section class="his-normal" style="display: block">
        <ul class="his-list" id="his_list">
          <!-- choose  unChoose   -->
          <li
            :class="['comic-item', item.choose]"
            v-for="item in historyList"
            :key="item.comicId"
          >
            <router-link :to="'/detail/' + item.comicId" class="comic-link">
              <div class="comic-cover">
                <img
                  class="cover-image"
                  :src="item.icon"
                  :alt="item.comicTitle"
                />
              </div>
              <div class="comic-info">
                <strong class="comic-title">{{ item.comicTitle }}</strong>
                <small class="comic-author"
                  >{{ item.author || item.artist }}</small
                >
                <small class="comic-update"
                  >{{ item.updateChapter || "..." }}</small
                >
                <div class="comic-continue">
                  <i class="continue-img star"></i>
                  <small>{{ item.buyCount }}</small>
                </div>
              </div>
            </router-link>

            <div class="comic-choose" @click="selectOne(item)">
              <i class="icon-choose"></i>
              <i class="icon-unChoose"></i>
            </div>
          </li>
        </ul>
      </section>
    </div>
  </article>

  <!--  -->
  <TipCmp ref="tip" @loadMore="handleScroll" />
  <!--  -->
  <BackToTopCmp />
</template>
<script>
import TopCmp from "../common/TopCmp.vue";
import TipCmp from "../common/ListTipCmp.vue";
import BackToTopCmp from "../common/BackToTopCmp.vue";
import apiUtils from "../utils/ApiUtil.vue";
import { decryptImageUrl } from "../utils/AES.vue";
import LoadingCmp from "../common/LoadingCmp.vue";
export default {
  data() {
    return {
      title: "",
      historyList: [],
    };
  },
  methods: {
    async getComicData(page = 1, pageSize = 6) {
      //
      if (!this.$refs.tip.canScroll()) {
        return;
      }
      // //
      this.$refs.loading.show();
      // //
      this.$refs.tip.loadingStart();

      try {
        var data = await apiUtils.getBuyList({ page: page, size: pageSize });

        //
        await Promise.all(
          data.dataList.map(async (item) => {
            item.icon = await decryptImageUrl(item.icon);
          })
        );

        this.historyList = this.historyList.concat(data.dataList);
         

        const total = data.total;
        const maxPage = Math.max(Math.ceil(total / pageSize), 1);
        this.currentPage = page;
        //
        this.$refs.tip.loadingEnd(
          this.currentPage == maxPage,
          this.historyList.length > 0
        );
        //
        this.$refs.loading.hide();
      } catch (error) {
        this.$refs.tip.hasError();
      }
    },
    handleScroll() {
      this.getComicData(this.currentPage + 1);
    },
  },
  components: {
    TopCmp,
    TipCmp,
    BackToTopCmp,
    LoadingCmp,
  },
  mounted() {
    this.$setMetaData("");
    this.getComicData(1);
    // window.addEventListener("scroll", this.handleScroll);
  },
  unmounted() {
    // window.removeEventListener("scroll", this.handleScroll);
  },
};
</script>
<style scoped>
.main .mod-his .his-normalfuck {
  background-color: #f5f5ee;

  padding-top: 1px;
  padding-bottom: 1px;
}
</style>
