<!--  -->
<template>
  <li
    :class="[
      'comic-pic-item',
      { 'pic-unloaded': !loaded },
      { 'pic-loaded': loaded },
    ]"
    :index="index"
    :seq="seq"
    :total="total"
  >
    <div class="comic-pic-pad" :style="{ paddingTop: padTop }">
      <div class="err-info" v-if="loadError">
        <span>~</span>
        <a class="reload-btn" @click="reloadImage"></a>
      </div>
    </div>
    <div class="comic-pic-box">
      <img
        class="comic-pic"
        :key="imageKey"
        :src="realImg"
        @load="onImageLoad"
        @error="onImageError"
        :data-timeout="imageTimeout"
        ref="image"
      />
    </div>
  </li>
</template>
<script>
import { decryptImageUrl } from "../utils/AES.vue";
export default {
  props: {
    //
    currentIndex: {
      type: Number,
      required: true,
    },
    //
    index: {
      type: Number,
      required: true,
    },
    //
    seq: {
      type: String,
      required: true,
    },
    timeout: {
      type: Number,
      default: 0,
    },
    imageUrl: {
      type: String,
      required: true,
    },
    scrollToMe: {
      type: Boolean,
      default: false,
    },
    total: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {
      padTop: "110%",
      imageTimeout: 2000,
      loadError: false,
      loaded: false,
      imageKey: 0,
      realImg: "",
    };
  },
  async mounted() {
    // 
    // this.realImg = await decryptImageUrl(this.imageUrl);
   // this.onCurrentIndexChange();
  },
  methods: {
    onImageLoad() {
      if (this.$refs.image) {
        this.padTop = `${
          (this.$refs.image.clientHeight / this.$refs.image.clientWidth) * 100
        }%`;
        this.loaded = true;
        this.imageKey = Date.now();
        //emit event
        this.$emit("loaded", this.index);
      } else {
        // handle the case where this.$refs.image is null
        console.warn("Image element not found");
      }
    },

    onImageError() {
      this.loadError = true;
    },
    async reloadImage() {
      this.loadError = false;
      // key1
      this.realImg = await decryptImageUrl(this.imageUrl);
      this.imageKey += 1;
    },
    //currentIndex 
    onCurrentIndexChange() {
      var curr = Number(this.currentIndex);

      //5
      if (curr + 5 >= this.index && !this.loaded &&
        curr - 5 <= this.index) {
        this.reloadImage();
      }
    },
  },

  //currentIndex
  watch: {
    currentIndex: {
      handler: "onCurrentIndexChange",
      immediate: true,
    },
  },
};
</script>
